.backdrop {
    position: fixed;
    top: 0;
    left: 0;  
    width: 100%;
    height: 100%;
    padding: 10% 15px; 
    background-color: rgba(255, 184, 202, 0.4);
    overflow-y: scroll;
    z-index: 999;
    opacity: 1;
    visibility: visible;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
     
    &--hidden {
      opacity: 0;
      pointer-events: none;
      visibility: hidden;
    }
}
  
  .modal {
    position: relative;
  
    margin: auto;
    padding: 40px;
  
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    background-color: #fff;
    border-radius: 1%;
  
    @media screen and (min-width: 480px) {
      width: 450px;
    }
  
    @media screen and (min-width: 1280px) {
      width: 528px;
    }
  
    &--bgc {
      background-image: url(data:image/jpeg;base64,);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }

    &--bggn {
      background-image: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSXMLNu_tM8reA2PEMlgQedam-SednJnfaAPQ&usqp=CAU);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }

  
    &__btn {
        position: absolute;
        top: 15px;
        right: 15px;
        width: 25px;
        height: 25px;
        transform: scale(1);
        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
        padding: 0;
        border: none;
        border-radius: 50%;
        cursor: pointer;       
  
      &:hover,
      &:focus {
        transform: scale(1.1);
        background-color: var(--bg-mobile-menu-cl);
      }
    }
  
    &__btn-icon {
      width: 11px;
      height: 11px;
    }
}
  
  .backdrop--hidden > .modal {
    transform: scale(1.1);

    @media screen and (min-width: 1280px) {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }

  .backdrop--hid > .modal {
    transform: scale(1.1);

    @media screen and (min-width: 1280px) {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }


